export function renderPagination(containerEl, currentPage, totalPages, onPageClick) {
  containerEl.innerHTML = '';
  if (totalPages <= 1) return;

  const pages = getPaginationRange(currentPage, totalPages);

  pages.forEach(page => {
    if (page === '...') {
      const ellipsis = document.createElement('span');
      ellipsis.classList.add('pagination-ellipsis');
      ellipsis.textContent = '...';
      containerEl.appendChild(ellipsis);
    } else {
      const span = document.createElement('span');
      span.classList.add('pagination-num');
      if (page === currentPage) span.classList.add('is-active');
      span.textContent = page;
      span.addEventListener('click', () => onPageClick(page));
      containerEl.appendChild(span);
    }
  });
}

function getPaginationRange(current, total) {
  const SIBLINGS = 1; // pages shown on each side of current
  const BOUNDARY = 1; // pages shown at start/end

  // If total pages fit comfortably, show all
  if (total <= SIBLINGS * 2 + BOUNDARY * 2 + 3) {
    return Array.from({ length: total }, (_, i) => i + 1);
  }

  const pages = [];
  const leftSibling = Math.max(current - SIBLINGS, BOUNDARY + 1);
  const rightSibling = Math.min(current + SIBLINGS, total - BOUNDARY);

  const showLeftEllipsis = leftSibling > BOUNDARY + 2;
  const showRightEllipsis = rightSibling < total - BOUNDARY - 1;

  // Always show first page(s)
  for (let i = 1; i <= BOUNDARY; i++) {
    pages.push(i);
  }

  // Left ellipsis or fill gap
  if (showLeftEllipsis) {
    pages.push('...');
  } else {
    for (let i = BOUNDARY + 1; i < leftSibling; i++) {
      pages.push(i);
    }
  }

  // Current page neighborhood
  for (let i = leftSibling; i <= rightSibling; i++) {
    pages.push(i);
  }

  // Right ellipsis or fill gap
  if (showRightEllipsis) {
    pages.push('...');
  } else {
    for (let i = rightSibling + 1; i <= total - BOUNDARY; i++) {
      pages.push(i);
    }
  }

  // Always show last page(s)
  for (let i = total - BOUNDARY + 1; i <= total; i++) {
    pages.push(i);
  }

  return pages;
}
